@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Mulish', sans-serif !important}.navbar{background-color:#fff !important;box-shadow:-5px -15px 15px #333}.navbar .navbar-brand{font-size:1.9rem !important;color:#3498db !important}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar a:hover{color:#3498db !important}.menu_active{font-weight:bold;border-bottom:1px solid #565387}.brand-name{color:#3498db}#header{width:100%;height:75vh;margin-top:50px}#header h1{margin:0 0 10px 0;font-size:40px;font-weight:700;color:black}#header h2{color:#484848;font-size:20px}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1px;display:inline-block;padding:7px 24px;border-radius:50px;color:#3498db;border:2px solid #3498db;text-decoration:none;transition:0.5s}#header .btn-get-started:hover{background:#3498db;color:#fff}.header-img{text-align:right}#header .animeted{animation:up-down 2s ease-in-out infinite alternate-reverse both;background:transparent}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(15px);transform:translateY(15px)}100%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}@keyframes up-down{0%{-webkit-transform:translateY(15px);transform:translateY(15px)}100%{-webkit-transform:translateY(-15px);transform:translateY(-15px)}}.contact_div{width:100%;height:68.5vh}footer{color:#292727;margin-bottom:0}

